.App {
  text-align: center;
  cursor: url(./assets/cursor_arrow.png) 20 20, auto;
}

.clickable{
  cursor: url(./assets/cursor_pointer.png) 20 20, pointer;
}

button{
  width: 100px;
  padding: 15px;
  margin: 15px;
  text-align: center;
  display: block;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.intropage{
  display: flex;
  flex-direction: row;
}

#intropage-left{
  border: #61dafb 1px solid;
  height: 100vh;
  width: 60vw;
}

#intropage-right{
  border: #61dafb 1px solid;
  height: 100vh;
  width: 50vw;
}

.intropage-mice{
  height: 20%;
  display: grid;
  grid-template-columns: [first] 20% [line2] auto [line3] 40% [end];
  grid-template-rows:[begin] 10% [row1-start] 80% [row1-end];

}

.intropage-mouse{
  
  align-self: end;
  grid-row-start: row1-start;
  grid-row-end: row1-end;
}

#intropage-mouse-1{
  /* height: 10%; */
  /* position: absolute;
  left: 15%;
  bottom: 20%; */
  
  grid-column-start: line2;
  grid-column-end: line3;

}

#intropage-mouse-2{
  /* height: 10%; */
  /* position: absolute;
  left: 30%;
  bottom: 20%; */
  
  grid-column-start: line3;
  grid-column-end: end;

}

/* .btn-deactive{
  font-size: 2rem;
} */

.btn-active{
  width: 50px;
  font-size: 3rem;
}

#gamepage{
  display: grid;
  grid-template-columns: [first] 15% [line2] auto [line3] 15% [end];
  grid-template-rows: [row1-start] 15% [row1-end] 15% [third-line] auto [last-line];
}

#gamepage-left{
  grid-column-start: first;
  grid-column-end: line2;
  justify-self: end;

}

#gamepage-right{
  grid-column-start: line3;
  grid-column-end: end;
  justify-self: start;

}


#gamepage-middle{
  grid-column-start: line2;
  grid-column-end: line3;
  justify-self: center;

}

#chatboard{
  padding-top: 20%;
  display: grid;
  grid-template-columns: [first] 15% [line2] auto [line3] 15% [end];
   
}

#chatboard-rule{
  width: 70%;
  height: 200px;
  border: #61dafb 1px solid;

  grid-column-start: line2;
  grid-column-end: line3;
  justify-self: center;
  
}

#chatboard-left{
  grid-column-start: first;
  grid-column-end: line2;
  justify-self: end;
  align-self: center;

}

#chatboard-right{
  grid-column-start: line3;
  grid-column-end: end;
  justify-self: start;
  align-self: center;
  
}